.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	z-index: 100;
	background: rgba(0,0,0,.34);
	.logo {
		position: relative;
		clear: both;
		overflow: hidden;
		margin: 5px 30px 0 0;
		a {
			display: block;
			height: 100%;
			img {
				float: left;
			}
		}
		.logo_dt {
			float: left;
			position: relative;
			margin-left: 10px;
			padding-top: 2px;
			padding-left: 11px;
			height: 35px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				top: 3px;
				left: 0;
				width: 1px;
				height: 17px;
				background: #dedede;
			}
		}
		.logo_dt.txt {
			padding-top: 0;
			height: auto;
			font-size: 21px;
			letter-spacing: -.5px;
			line-height: 26px;
		}
	}
	.nav {
		overflow: hidden;
		width: 100%;
		height: 52px;
		max-width: 1100px;
		min-width: 1100px;
		margin: 0 auto;
		box-sizing: border-box;
		z-index: 100;
		.lt {
			float: left;
		}
		.rt {
			float: right;
		}
		.menu {
			overflow: hidden;
			float: left;
			> li {
				float: left;
				height: 50px;
				margin-right: 48px;
				line-height: 50px;
				>a {
					display: block;
					font-size: 14px;
					text-decoration: none;
					color: rgba(74,74,74,.7);
				}
				&:hover {
					.sub_menu {
						display: block;
					}
                }
                &:focus {
                    .sub_menu {
                        display: block;
                    }
                }
			}
			> li.on {
				>a {
					font-weight: bold;
					color: rgba(74,74,74,1);
				}
			}
		}
		.sub_menu {
			position: absolute;
			top: 60px;
			display: none;
			margin-left: -15px;
			padding: 10px;
			background-color: #00b4ff;
			&:after {
				content: "";
				position: absolute;
				top: -5px;
				left: 50%;
				width: 0;
				height: 0;
				margin-left: -5px;
				border-bottom: 5px solid #00b4ff;
				border-left: 5px solid transparent;
				border-right: 5px solid transparent;
			}
			a {
				display: block;
				padding: 10px;
				line-height: 1.3;
				font-size: 13px;
				color: #fff;
			}
		}
		.btn_area {
			float: left;
			margin-top: 10px;
			.btn {
				display: inline-block;
				height: 28px;
				line-height: 28px;
				text-align: center;
				border: 1px solid rgba(255,255,255,.8);
				font-size: 14px;
				color: rgba(255,255,255,.8);
				padding: 0 15px;
				&:hover {
					color: #fff;
					border-color: #fff;
				}
				+ {
					.btn {
						margin-left: 4px;
					}
				}
			}
		}
    }
    &.active {
        background: rgba(255, 255, 255, .95);
    }
}
.spot {
    position: relative;
    height: 720px;
    background: url(https://static.wehago.com/html/landing/img/bong/bg_spot.png) 50% 0 no-repeat;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 50%;
		height: 720px;
		background: url(https://static.wehago.com/html/landing/img/bong/bg_spot_repeat.png) 0 0 repeat-x;
		z-index: -1;
	}
	&:after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 50%;
		height: 720px;
		background: url(https://static.wehago.com/html/landing/img/bong/bg_spot_repeat.png) 0 0 repeat-x;
		z-index: -1;
	}
    .inner {
        .showcase {
            position: relative;
            height: 100%;
            padding-top: 393px;
            .left_text {
                margin-top: 25px;
                h3 {
                    font-size: 26px;
                    font-weight: normal;
                    color: #fff;
                    line-height: 42px;
                    letter-spacing: -0.75px;
                    strong {
						font-family: douzonetitle;
						font-size: 42px;
						font-weight: normal;
                    }
                }
                a {
                    display: inline-block;
                    margin-top: 5px;
                    vertical-align: top;
                }
            }
            .right_text {
                position: absolute;
                top: 393px;
                right: -150px;
            }
        }
    }
}
.section {
	&.info {
		padding-top: 135px;
		padding-bottom: 109px;
		text-align: center;
		.inner {
			.img_box {
				overflow: hidden;
				width: 965px;
				margin: 53px auto 0;
				.img_network,
				.img_share {
					float: left;
					width: 464px;
					height: 551px;
				}
				.img_share {
					margin-left: 37px;
				}
			}
		}
	}
}
.section {
	&.share {
		position: relative;
		height: 595px;
		padding-top: 106px;
		background-color: #f0f2f6;
		text-align: center;
		&:before {
			z-index: 1;
			content: '';
			position: absolute;
			top: 0;
			left: 20px;
			width: 305px;
			height: 144px;
			background: url(https://static.wehago.comhttps://static.wehago.com/html/landing/img/bong/bong_section_03_l.png) 0 0 no-repeat;
		}
		&:after {
			z-index: 1;
			content: '';
			position: absolute;
			bottom: 0;
			right: 20px;
			width: 237px;
			height: 92px;
			background: url(https://static.wehago.comhttps://static.wehago.com/html/landing/img/bong/bong_section_03_r.png) 0 0 no-repeat;
		}
		.namecard_box {
			overflow: hidden;
			padding-top: 119px;
			.namecard {
				position: relative;
				float: left;
				width: 350px;
				height: 250px;
				margin-left: 25px;
				background-color: #fff;
				border-radius: 16px;
				&:first-child {
					margin-left: 0;
				}
				.people_thumb {
					position: absolute;
					top: -58px;
					left: 50%;
					width: 107px;
					height: 107px;
					margin-left: -53.5px;
				}
				.namecard_info {
					padding-top: 81px;
					strong {
						display: block;
						font-size: 24px;
						font-weight: 900;
						color: #1f2b3e;
						letter-spacing: -0.05em;
					}
					p {
						margin-top: 5px;
						font-size: 15px;
						color: #1e1e1e;
						letter-spacing: -0.01em;
						line-height: 24px;
						word-break: keep-all;
						word-wrap: break-word;
					}
				}
			}
		}
	}
}
.section {
	&.infra {
		position: relative;
		height: 750px;
		padding-top: 150px;
		text-align: center;
		.bg_main {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: url(https://static.wehago.com/html/landing/img/bong/bg_infra.png) 50% 0 no-repeat;
			z-index: -1;
		}
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 50%;
			height: 750px;
			background: url(https://static.wehago.com/html/landing/img/bong/bg_infra_repeat_left.png) 0 0 repeat-x;
			z-index: -2;
		}
		&:after {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			width: 50%;
			height: 750px;
			background: url(https://static.wehago.com/html/landing/img/bong/bg_infra_repeat_right.png) 0 0 repeat-x;
			z-index: -2;
		}
		.inner {
			h4 {
				font-weight: normal;
				color: #fff;
			}
			.img_infra {
				position: relative;
				margin-top: 92px;
				.text_box {
					position: absolute;
					strong {
						font-weight: 900;
						color: #fff;
					}
					p {
						margin-top: 11px;
						font-size: 14px;
						color: #fff;
						line-height: 20px;
					}
					&.top_left {
						top: 59px;
						left: 97px;
						text-align: right;
					}
					&.top_right {
						top: 59px;
						left: 742px;
						text-align: left;
					}
					&.bottom_left {
						top: 240px;
						left: 97px;
						text-align: right;
					}
					&.bottom_right {
						top: 240px;
						left: 742px;
						text-align: left;
					}
				}
			}
		}
	}
}
.section {
	&.community {
		padding: 87px 0 56px;
		background-color: #f0f2f6;
		.community_list {
			ul {
				display: flex;
				li {
					flex: 1;
					text-align: center;
					.ico_list {
						display: block;
						position: relative;
						width: 57px;
						height: 57px;
						margin: 0 auto;
						img {
							position: absolute;
							top: 50%;
							left: 50%;
							transform: translate(-50%, -50%);
						}
					}
					strong {
						display: block;
						margin-top: 16px;
						font-size: 18px;
						font-weight: 900;
						color: #1f2b3e;
					}
					.ico_list_text {
						display: block;
						margin-top: 5px;
						font-size: 14px;
						color: #1f2b3e;
						line-height: 18px;
						letter-spacing: -0.05em;
					}
					&.exception {
						.ico_list {
							img {
								margin-left: 5px;
							}
						}
					}
					&.exception2 {
						.ico_list {
							img {
								margin-left: 10px;
							}
						}
					}
				}
				& + ul {
					margin-top: 63px;
				}
			}
		}
	}
}
.footer {
	padding: 27px 0 53px;
	.footer_info {
		.copyright {
			font-family: "Nanum Square", "돋움" , Dotum, Helvetica, "Apple SD Gothic Neo", sans-serif !important;
			font-size: 13px;
			line-height: 20px;
			color: #000;
			text-align: center;
			.bar {
				display: inline-block;
				width: 1px;
				height: 12px;
				background: #cccccc;
				margin: 4px 14px 0;
				vertical-align: top;
			}
			> em {
				display: inline-block;
				font-family: "Nanum Square", "돋움" , Dotum, Helvetica, "Apple SD Gothic Neo", sans-serif !important;
				vertical-align: top;
				a {
					font-family: "Nanum Square", "돋움" , Dotum, Helvetica, "Apple SD Gothic Neo", sans-serif !important;
					color: #000;
				}
			}
		}
	}
}

/* Animation */
.section {
	&.active {
		animation: parallax 1s ease forwards 1;
	}
}

/* keyframes */
@keyframes parallax {
	0% {
		opacity: 0;
		transform: translateY(50px);
	}
	100% {
		opacity: 1;
		transform: translateY(0px);
	}
}